*{
    margin: 0 ;
    padding: 0 ;
}
html{
    font-size: 62.5%;
    background-color: black;


}
.App{
    background-color: black;
    color: white;
}
.logo{
    position: fixed;
    margin-top: 3rem;
    margin-left: 3rem;
    width: 350px;
}
.logo img{
    width: 100%;
}

/* SideBar */
.sidebar {
    /* position: fixed; */
    width:100%;
    /* height: 100vh; */
    background: black;
    transition: all 0.5s ease;
  }
  
  .sidebar header {
    font-size: 22px;
    color: white;
    line-height: 70px;
    text-align: center;

    user-select: none;
  }
  .sidebar ul{
      margin-top: 16rem;
    list-style: none;

  }
  .sidebar ul li{
margin-bottom: 3rem;
  }
  .sidebar ul a {
    text-decoration: none;
    display: block;
    height: 100%;
    margin: auto;
    width: 80%;
    line-height: 65px;
    font-size: 20px;
    color: white;
    padding-left: 40px;
    box-sizing: border-box;
    border-bottom: 1px solid black;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    transition: 0.4s;
  }

  ul li:hover a {
    padding-left: 30px;
    border: 4px solid red;
    border-radius: 10px;
  }
  
  .sidebar ul a i {
    margin-right: 16px;
  }

  .icons{
      font-size: 2rem !important;
      margin-right: 2rem;
  }
  .iconPlay{
      margin-right: 0.3rem !important;
  }
/* SideBar Ends */

img{    padding: 0 !important;
    margin: 0 !important;}
.banner{
    background-color: black;
    height: 50vh;
}

.banner_content{
    /* height: 100%; */
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
}
.bannerContainer{
    position: relative;
}
.bannerContainer:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    z-index: 1;
    background: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.76) 15.54%,
      rgba(0, 0, 0, 0.192) 60.23%,
      rgba(0, 0, 0, 8e-5) 100%
    );
  }
.banner_title{

    font-size: 6rem;
    font-weight: 800;
    padding-bottom: 1rem;
    margin: 3rem;
    margin-bottom: 1.3rem;
}

.banner_description{
    margin-left: 3rem;
    font-size: 1.5rem;
    line-height: 2rem;
    /* padding-top: 1rem; */
}

.banner_img{
    width: 100%;

}

.banner_img img{
    width: 100%;
    object-fit: contain;
}
.row_posters{
    display: flex;
    overflow-x: scroll;
    overflow-y:hidden ;
    padding: 10px;

}
.row_posters::-webkit-scrollbar{
    display: none;
}
.row_posters a{
    width: 100%;
    height: 100%;
}
.row_poster{
    object-fit: contain;
    /* width: 100%; */
    height: 150px;
    margin-right: 15px !important;
}

.preview_container{
    width: 100%;
    height: 100vh;
}
.preview_container{
    /* background-image:linear-gradient(to bottom, rgba(245, 246, 252, 0.342), rgba(0, 0, 0, 0.73)), url('./img/banner_img.jpeg');
    background-position: center;
